import React from "react"
import timelineStyles from "./timeline.module.css"
import JSONData from "../data/timeline.json"

export default function Timeline() {
    return(
        <div className="timeline">
            {/* {console.log(JSONData)} */}
            {JSONData.map((data, index) => {
                return(
                    <div key={index} className="timelineItem">
                        <h3 className={timelineStyles.header}>{data.role}, <a href={data.url} target="_blank" rel="noopener noreferrer">{data.org}</a></h3>
                        <p>{data.years} | {data.location}</p>
                        <ul>
                            {data.desc.map((bullet, index) => <li key={index}>{bullet}</li> )}
                        </ul>
                    </div>
                )
            })}
            <hr />
        </div>
    )
}
