import React from "react"
import { Helmet } from "react-helmet"
import Layout from "../components/layout"
import Timeline from "../components/timeline"
import Projects from "../components/projects"
// https://github.com/react-icons/react-icons
import { FaTwitter, FaGithub, FaLinkedin } from 'react-icons/fa';
import "../styles/global.scss"
import { graphql, useStaticQuery } from "gatsby"


export default function Home() {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            img
            url
          }
        }
      }
    `
  )
  return (
    <div>
      <Helmet>
        <title>{data.site.siteMetadata.title}</title>
        <meta name="description" content={data.site.siteMetadata.description} />
        <link rel="canonical" href={data.site.siteMetadata.url} />
        <meta property="og:title" content={data.site.siteMetadata.title} />
        <meta property="og:description" content={data.site.siteMetadata.description} />
        <meta property="og:image" content={data.site.siteMetadata.img} />
        <meta property="og:url" content={data.site.siteMetadata.url} />
      </Helmet>
      <Layout>
        <article>
          <section id="intro">
            <h2>Hi there.</h2>
            <p>My name is Rob and my background is in digital and visual communication.</p>
            <p>For most of my career, I've worked for newspapers doing website development, content production and building digital tools. I'm also interested in data analysis and supporting local journalism.</p>
            <p>When I'm not working, you can find me exploring the outdoors or trying to learn something new.</p>
            <p>For more on my professional background, see my resume below. You can also follow me on social media for updates.</p>
            <div className="contact-icons">
              <a href="https://twitter.com/robertrdenton"><FaTwitter className="contact-icon" /></a>
              <a href="https://linkedin.com/in/robertrdenton"><FaLinkedin className="contact-icon" /></a>
              <a href="https://github.com/robertdenton"><FaGithub className="contact-icon" /></a>
            </div>
          </section>
          <hr/>
          <section id="timeline">
            <h2>My work</h2>
            <Timeline />
          </section>
          <section id="projects">
            <h2>Past projects</h2>
            <Projects />
          </section>
        </article>
      </Layout>
    </div>
  )
}
